import {
	getUserDetail
} from '../../config/api.js';
export default {
	data() {
		return {
			categoryPath: '/pages/category/categoryDetail',
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 0,
			flist: [],
			slist: [],
			tlist: [],
			categoryList: [],
			url: this.global.PICTURE_URL,
			folderName: "category"
		};
	},
	onLoad(option) {
		this.loadData();
	},
	onPullDownRefresh(){
		this.flist =[],
		this.slist =[],
		this.tlist =[],
		
		this.loadData();
	},

	methods: {
		async loadData() {
			this.$minApi.uniapp_post(this.$base_api.getCategory, {
				unitNo: this.$base_api.UNIT_NO,
				mallId: this.$base_api.MALL_ID
			}).then(res => {
				this.categoryList = res.data;
				var res = this.categoryList.filter(item => {
					return item.parentId == "0"
				});
				this.currentId = res[0].categoryId
				this.categoryList.forEach(item => {
					item.bannerUrl = this.$base_api.fileReview + '?mallId=' + this.$base_api.MALL_ID + '&folderName=' + this.folderName +
						'&fileName=' + item.bannerUrl;
					if (item.parentId == "0") {
						this.flist.push(item)
						this.slist.push(item)
					} else {
						this.tlist.push(item)
					}
				})

			})

		},

		tabtap(item) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}
			this.currentId = item.categoryId;
			console.log(item)
			console.log(this.slist)
			let index = this.slist.findIndex(sitem => sitem.categoryId === item.categoryId);
			this.tabScrollTop = this.slist[index].top;
			if (this.tabScrollTop === undefined) {
				this.tabScrollTop = 151
			}
		},
		//右侧栏滚动
		asideScroll(e) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item => item.top <= scrollTop).reverse();
			if (tabs.length > 0) {
				console.log(tabs)
				this.currentId = tabs[0].categoryId;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize() {
			let h = 0;
			this.slist.forEach(item => {
				let view = uni.createSelectorQuery().select("#main-" + item.categoryId);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(path, item) {
			console.log("item",item)
			uni.navigateTo({
				url: path + '?item=' + encodeURIComponent(JSON.stringify(item))
			});
		}
	}
};
